
import {
    getStorageSync
} from '@/utils/local.js'
import {BASE_URL} from '@/utils/common.js'
module.exports = (vm) => {
    // 
    uni.$u.http.setConfig((config) => {
        /* config */
        config.baseURL = BASE_URL; /*  */
        return config
    })
	
	// 
	uni.$u.http.interceptors.request.use((config) => { // async await 
	    // dataundefined{}
	    config.data = config.data || {}
		const token  = getStorageSync('token')
		if(token) {
			config.header['Authori-zation'] = `Bearer ${token}`
		}
	    return config 
	}, config => { // async await 
	    return Promise.reject(config)
	})
	
	// 
	uni.$u.http.interceptors.response.use((response) => { /*  async await */
		const data = response.data
		if (data.status !== 200) { 
			uni.showToast({
				title:data.msg,
				icon:'none'
			})
			return Promise.reject(data)
		}
		return Promise.resolve(data)
	}, (response) => { 
		//  statusCode !== 200
		return Promise.reject(response)
	})
}